@import url(https://fonts.googleapis.com/css?family=Yesteryear|Open+Sans|Roboto|Raleway|Abril+Fatface);
body, html {
  font-size: 12px;
  height: 100%;
}



@media (min-width: 576px) {
  body, html {
    font-size: 16px;
  }
}

@media (min-width: 1200px) {
  body, html {
    font-size: 20px;
  }
}

/* Standard ParallaxPage Styling  */
.ParallaxPage{
	height: 85vh;
	overflow: scroll;
	margin: auto;
}
::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
		height: 0px;  /* remove scrollbar space */
    background: transparent;  /* make scrollbar invisible */
}

/*Standard container styling */

.ParallaxPage .container{
	background-color: inherit;
  color: inherit;
  height: 100%;
  overflow: auto;
	padding-top: 2vh;
	padding-bottom: 2vh;
}

.ParallaxPage .row{
	margin-left: 5vw;
	margin-right: 5vw;
}


@media (max-width: 576px) {

  .ParallaxPage .row {
    margin-left: 0px;
    margin-right: 0px;
  }

}

/* Bootstrap Override */

.ParallaxPage a {
	color: inherit;
}

/* Custom styling */

.ParallaxPage {
	font-family: "Raleway";
}

.ParallaxPage h1{
	font-size: 3rem;
}

.ParallaxPage h2{
	font-size: 2.5rem;
}

.ParallaxPage h3{
	font-size: 2rem;
}

.ParallaxPage h4{
	font-size: 1.5rem;
}


.bgimg1, .bgimg2, .bgimg3, .bgimg4 {
	position: relative;
	background-attachment: fixed;
  background-position: center;
	height: 60vh;
	background-size: cover;
	background-repeat: no-repeat;
  justify-content: center;
  text-align: center;
}

.bgimg1 {
	background-image: url(/restless-anglers/static/media/boat.a56e54ef.jpg);
	background-color: rgb(48, 48, 48);
	height: 80vh;
}

.bgimg2 {
	background-image: url(/restless-anglers/static/media/albumfront.3b428ee5.png);
	background-color: rgb(130, 130, 130);
	background-position: top;
}

.bgimg3 {
	background-image: url(/restless-anglers/static/media/albumback.a100c176.png);
	background-color: rgb(48, 48, 48);
	background-position: top;
}

.bgimg4 {
	background-image: url(/restless-anglers/static/media/boat.a56e54ef.jpg);
	background-color: rgb(130, 130, 130);
}

.captionline {
	position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  text-align: center;
  color: white;
}

.captionbox {
  background-color: rgba(0,0,0,.8);
  padding: 15px;
  font-size: 3rem;
  letter-spacing: 10px;
}

.viewcap {
	background-color: black;
}

/* Standard Navigation Bar Styling */
.NavigationBar {
	height: 100%;
	font-family: "Raleway";
	font-size: 3vh;
	min-height: 6vh;
}

#logo {
	height: 6vh;
  margin-right: 2vw;
  margin-left: 2vw;
}

/* Bootstrap overrides */
.nav-link {
	color: inherit!important;
	border-bottom: 1px transparent solid;
}

.navbar {
	padding-top: 1vh;
	padding-bottom: 1vh;
}

.navbar-brand {
	padding-top: 0px;
	padding-bottom: 0px;
	border-bottom: 1px transparent solid;
}

/* Defualt Footer Styling */
.Footer {
  height: 6vh;
  font-size: 1.75vh;
  font-weight: lighter;
  width: 100%;
  padding-top: 1vh;
}

.Footer a {
  color: inherit;
  font-size: inherit;
  margin-left: 10px;
}

/* Bootstrap Overrides */

.App {
  justify-content: center;
  text-align: center;
}

